import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('InGameAssets')
export class InGameAssets extends Component {

    @property({ type: [Node] })
    candies: Node[] = []!;

    @property({ type: [Node] })
    blocks: Node[] = []!;

    @property({ type: [Node] })
    bombs: Node[] = []!;

    public static NodeMap: Map<string, Node> = new Map();

    onLoad() {
        this.candies.forEach((node, index) => {
            InGameAssets.NodeMap.set(`${node.name}`, node);
        });

        this.blocks.forEach((node, index) => {
            InGameAssets.NodeMap.set(`${node.name}`, node);
        });

        this.bombs.forEach((node, index) => {
            InGameAssets.NodeMap.set(`${node.name}`, node);
        });
    }
}
